export default {

    mounted(){
        window.addEventListener('scroll', this.handleScroll)
    },
    
    methods: {
        handleScroll(){
            let y = window.scrollY
            let scroll = document.querySelector('.scroll-container').style

            y > 0 ? scroll.opacity = '0' : scroll.opacity = '1'
        }

    }

}
